*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.bg-nav{
  background-color: rgba(231,71,60,.97);
}

.bg-login, .bg-content{
  background-color: rgba(242, 155, 149, 0.97);
}

/* .content{
  height: 100vh;
} */

.navbar{
  background-color: rgba(249, 116, 107, 0.97) !important;
}

.register, .login{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
}

/* Transaction Page */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}
/* Transaction Page */


/* Antd icons */
.anticon svg{
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
  color: red;
}

.inactive-icon{
  color: white;
}

.switch-icons{
  border: 1px solid rgba(0, 0, 0, 0.61);
  border-radius: 5px;
  padding: 10px 15px;
}